// app/cart/page.tsx
// Version: 2.4.0 - 2025-11-05 - EMERALD THEME STYLING
// âœ… Updated cart items with emerald-themed styling
// âœ… Rounded borders, gradient backgrounds, better contrast
// âœ… Improved image display with bordered containers
// Previous: Debug logging for customImage

'use client'

import { Metadata } from 'next'
import { useState, useEffect } from 'react'
import Link from 'next/link'
import { ArrowLeft, Trash2 } from 'lucide-react'


interface CartItem {
  productId: string
  name: string
  sku: string
  price: number
  quantity: number
  options: {
    size?: string
    background?: string
    lightBase?: string
    giftStand?: string
    customText?: string | { line1?: string; line2?: string }
    rawImageUrl?: string
    imageUrl?: string
    maskedImageUrl?: string
  }
  sizeDetails?: {
    name: string
    price: number
  }
  customImage?: {
    dataUrl: string
    filename: string
    mimeType: string
    fileSize: number
    width: number
    height: number
  }
  cockpit3d_id?: string
  dateAdded: string
}

export default function CartPage() {
  const [cart, setCart] = useState<CartItem[]>([])
  const [total, setTotal] = useState(0)
  const [loading, setLoading] = useState(true)
  const [checkoutLoading, setCheckoutLoading] = useState(false)

  useEffect(() => {
    loadCart()
  }, [])

  function loadCart() {
    const cartData = JSON.parse(localStorage.getItem('cart') || '[]')
    
    console.log('ðŸ›’ Cart loaded:', {
      itemCount: cartData.length,
      items: cartData.map((item: any, i: number) => ({
        index: i,
        name: item.name,
        hasCustomImage: !!item.customImage,
        hasDataUrl: !!item.customImage?.dataUrl,
        dataUrlLength: item.customImage?.dataUrl?.length || 0,
        hasOptionsImages: !!(item.options?.rawImageUrl || item.options?.imageUrl)
      }))
    })
    
    setCart(cartData)
    
    const sum = cartData.reduce((acc: number, item: CartItem) => {
      const itemPrice = item.price || (item as any).totalPrice || 0
      return acc + (itemPrice * item.quantity)
    }, 0)
    setTotal(sum)
    setLoading(false)
  }

  function removeItem(index: number) {
    const newCart = cart.filter((_, i) => i !== index)
    setCart(newCart)
    localStorage.setItem('cart', JSON.stringify(newCart))
    
    const sum = newCart.reduce((acc, item) => {
      const itemPrice = item.price || (item as any).totalPrice || 0
      return acc + (itemPrice * item.quantity)
    }, 0)
    setTotal(sum)
    
    window.dispatchEvent(new Event('cartUpdated'))
  }

  function clearCart() {
    if (!window.confirm('Are you sure you want to clear your cart?')) return
    
    setCart([])
    setTotal(0)
    localStorage.removeItem('cart')
    window.dispatchEvent(new Event('cartUpdated'))
  }

  // Build Cockpit3D order structure
  function buildCockpitOrder(): any {
    const orderNumber = `CK-${Date.now()}`
    
    return {
      order_id: orderNumber,
      customer_name: '', // Will be filled in checkout
      email: '', // Will be filled in checkout
      phone: '', // Will be filled in checkout
      staff_user: 'Web Order',
      shipping_method: 'air',
      destination: 'customer_home',
      shipping_address: {}, // Will be filled in checkout
      billing_address: {}, // Will be filled in checkout
      items: cart.map(item => ({
        sku: item.sku || item.productId,
        qty: item.quantity,
        price: item.price || (item as any).totalPrice || 0,
        name: item.name,
        custom_options: buildCustomOptions(item),
        custom_image_url: item.customImage?.dataUrl || item.options.rawImageUrl || item.options.imageUrl || undefined
      })),
      total: total
    }
  }

  function buildCustomOptions(item: CartItem) {
    const options: Array<{ option_id: string; option_value: string }> = []
    
    // Helper to extract value from option (handles both string and object)
    const getOptionValue = (option: any): string => {
      if (!option) return ''
      if (typeof option === 'string') return option
      if (typeof option === 'object') return option.name || option.id || String(option)
      return String(option)
    }
    
    // Size
    if (item.options.size) {
      options.push({
        option_id: 'size',
        option_value: item.sizeDetails?.name || getOptionValue(item.options.size)
      })
    }
    
    // Background
    if (item.options.background) {
      options.push({
        option_id: 'background',
        option_value: getOptionValue(item.options.background)
      })
    }
    
    // Light Base
    if (item.options.lightBase) {
      options.push({
        option_id: 'lightBase',
        option_value: getOptionValue(item.options.lightBase)
      })
    }
    
    // Gift Stand
    if (item.options.giftStand) {
      options.push({
        option_id: 'giftStand',
        option_value: getOptionValue(item.options.giftStand)
      })
    }
    
    // Custom Text
    if (item.options.customText) {
      const text = typeof item.options.customText === 'string' 
        ? item.options.customText 
        : `${item.options.customText.line1 || ''} ${item.options.customText.line2 || ''}`.trim()
      
      if (text) {
        options.push({
          option_id: 'customText',
          option_value: text
        })
      }
    }
    
    return options
  }

  async function proceedToCheckout() {
    setCheckoutLoading(true)
    
    try {
      // Build order structure
      const orderData = buildCockpitOrder()
      
      // Store in session for checkout page
      sessionStorage.setItem('pendingOrder', JSON.stringify({
        cartItems: cart,
        subtotal: total,
        total: total,
        orderNumber: orderData.order_id,
        cockpitOrderData: orderData
      }))
      
      // Navigate to checkout
      window.location.href = '/checkout'
      
    } catch (error) {
      console.error('Checkout error:', error)
      alert('Failed to proceed to checkout. Please try again.')
      setCheckoutLoading(false)
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-900">
        <div className="spinner-border text-success" style={{ width: '3rem', height: '3rem' }}>
          <span className="visually-hidden">Loading...</span>
        </div>
      </div>
    )
  }

  if (cart.length === 0) {
    return (
      <div className="min-h-screen bg-gray-900 text-white">
        {/* Hero */}
        <section className="hero px-8 py-16 text-center">
          <div className="hero-content max-w-xl mx-auto">
            <h1 className="primary-header mb-4">Your Cart is Empty</h1>
            <p className="lead">Add some beautiful crystals to get started!</p>
          </div>
        </section>

        <div className="container mt-5">
          <div className="row">
            <div className="col-sm-8 mx-auto text-center">
              <p className="mb-4">Ready to create something special?</p>
              <Link href="/products" className="btn btn-primary rounded-lg px-4 py-3">
                Browse Our Designs
              </Link>
            </div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-900 text-white">
      
      {/* Hero Section */}
       <section className="hero px-8 py-16 text-center">
        <div className="container">
          <div className="hero-content text-center">
            <h1 className="primary-header">Your Cart</h1>
            <p className="lead mt-3">Review your selections and complete checkout</p>
          </div>
        </div>
      </section>
            
      {/* Breadcrumb */}
      <nav className="breadcrumbs py-4">
        <div className="container mx-auto px-4">
          <Link href="/" className="hover:text-brand-400 transition-colors">Home</Link>  
          <span className="mx-2 text-gray-600">/</span>          
          <Link href="/products" className="hover:text-brand-400 transition-colors">Products</Link>  
          <span className="mx-2 text-gray-600">/</span>
          <span className="text-text-tertiary">Cart</span>
        </div>
      </nav>
      
      {/* Cart Content */}
      <section className="container mt-4 mb-5 w-full px-4 mx-auto">
        
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">

          {/* Cart Items Column */}
          <div className="col-span-2">
            
            <div className="cart-items rounded-xl border border-gray-700/50 bg-gray-900/50 p-5 backdrop-blur-sm">
              
              <div className="flex justify-content-between align-items-center justify-between mb-4">
                
                <h3 className="text-emerald-400 font-bold">Your Items ({cart.length})</h3>
                
                <button
                  className="btn btn-outline-danger px-3 py-2 rounded-md btn-sm hover:bg-red-500/20 transition-colors hover:cursor-pointer"
                  onClick={clearCart}
                >
                  Clear Cart
                </button>

              </div>

              {cart.map((item, index) => (
                <div 
                  key={index} 
                  className="cart-item rounded-xl border border-emerald-500/30 bg-emerald-500/5 mb-4 p-4 hover:border-emerald-500/50 transition-all"
                >
                  <h4 className="mb-3 text-emerald-100 font-semibold"></h4>
                  
                  <div className="">
                    {/* Images Section */}
                    <div className="col-12 col-md-6 mb-3 mb-md-0 border-2 rounded-lg border-2 border-emerald-500/30 bg-gray-900/50">
                      {/* Custom Image from new format (customImage.dataUrl) OR old format (options.rawImageUrl) */}
                      {(item.customImage?.dataUrl || item.options?.rawImageUrl || item.options?.imageUrl || item.options?.maskedImageUrl) && (
                        
                        <div className="mb-3"><h6 className="small mb-2 text-emerald-300 font-semibold">Your Custom Image:</h6>

                          <div className="grid grid-cols-2 sm:grid-cols-3 items-start sm:gap-4 gap-6 ">

                            <div className="col-span-2 flex items-start gap-4">
                                <div className="w-48 h-48 max-sm:w-24 max-sm:h-24 shrink-0 bg-gray-100 p-3 rounded-md">
                                    <img 
                                      src={item.customImage?.dataUrl || item.options?.maskedImageUrl || item.options?.imageUrl || item.options?.rawImageUrl || ''}
                                      alt="Custom design"
                                      className="img-fluid w-full w-full h-full object-contain"
                                      style={{ maxHeight: '600px', width: 'auto', margin: '0 auto', display: 'block' }}
                                      onError={(e) => e.currentTarget.style.display = 'none'}
                                    />
                                    {item.customImage && (
                                      <small className="text-emerald-400/70 d-block mt-2 text-center">
                                        {item.customImage.width}x{item.customImage.height}px
                                      </small>
                                    )}
                                </div>
                                <div className="flex flex-col">
                                    <h3 className="text-base font-semibold text-slate-900">{item.name}</h3>
                                    <p className="text-sm font-medium text-slate-500 mt-2">{item.size}</p>
                                    {/* Selected Options */}
                                    {item.options && (
                                    <div className="mb-3">
                                        <h6 className="mb-2 text-emerald-300 font-semibold">Product Details:</h6>
                                        <ul className="list-unstyled small text-gray-300">
                                          {item.sizeDetails && (
                                            <li className="mb-1">
                                              <span className="text-emerald-400">Size:</span> <span className="text-gray-200">{item.sizeDetails.name}</span>
                                            </li>
                                          )}
                                          {item.options.background && (
                                            <li className="mb-1">
                                              <span className="text-emerald-400">Background:</span> <span className="text-gray-200">{
                                                typeof item.options.background === 'object' 
                                                  ? (item.options.background as any).name || (item.options.background as any).id
                                                  : item.options.background
                                              }</span>
                                            </li>
                                          )}
                                          {item.options.lightBase && (
                                            <li className="mb-1">
                                              <span className="text-emerald-400">Light Base:</span> <span className="text-gray-200">{
                                                typeof item.options.lightBase === 'object'
                                                  ? (item.options.lightBase as any).name || (item.options.lightBase as any).id
                                                  : item.options.lightBase
                                              }</span>
                                            </li>
                                          )}
                                          {item.options.giftStand && (
                                            <li className="mb-1">
                                              <span className="text-emerald-400">Stand:</span> <span className="text-gray-200">{
                                                typeof item.options.giftStand === 'object'
                                                  ? (item.options.giftStand as any).name || (item.options.giftStand as any).id
                                                  : item.options.giftStand
                                              }</span>
                                            </li>
                                          )}
                                          {(item.customImage?.dataUrl || item.options.rawImageUrl) && (
                                            <li className="mb-1 text-success">
                                              âœ“ Custom Image Added
                                            </li>
                                          )}
                                        </ul>
                                      </div>
                                    )}

                                    {/* Custom Text */}
                                    {item.options?.customText && (
                                      <div className="mb-3">
                                        <h6 className="mb-2 text-emerald-300 font-semibold">Custom Text:</h6>
                                        {typeof item.options.customText === 'string' ? (
                                          <p className="small mb-0 text-gray-300 italic">{item.options.customText}</p>
                                        ) : (
                                          <ul className="list-unstyled small text-gray-300">
                                            {item.options.customText.line1 && (
                                              <li><span className="text-emerald-400">Line 1:</span> {item.options.customText.line1}</li>
                                            )}
                                            {item.options.customText.line2 && (
                                              <li><span className="text-emerald-400">Line 2:</span> {item.options.customText.line2}</li>
                                            )}
                                          </ul>
                                        )}
                                      </div>
                                    )}
                                    <button
                                      className="btn btn-outline-danger text-center px-2 rounded-md btn-sm hover:bg-red-500/20 transition-colors hover:cursor-pointer"
                                      
                                    >
                                     <Trash2 /> Remove
                                    </button>
                                    <button
                                      onClick={() => removeItem(index)} 
                                      type="button" 
                                      className="mt-6 font-semibold text-red-500 text-xs flex items-center gap-2 shrink-0 cursor-pointer">
                                        <svg xmlns="http://www.w3.org/2000/svg" class="w-4 fill-current inline" viewBox="0 0 24 24">
                                            <path d="M19 7a1 1 0 0 0-1 1v11.191A1.92 1.92 0 0 1 15.99 21H8.01A1.92 1.92 0 0 1 6 19.191V8a1 1 0 0 0-2 0v11.191A3.918 3.918 0 0 0 8.01 23h7.98A3.918 3.918 0 0 0 20 19.191V8a1 1 0 0 0-1-1Zm1-3h-4V2a1 1 0 0 0-1-1H9a1 1 0 0 0-1 1v2H4a1 1 0 0 0 0 2h16a1 1 0 0 0 0-2ZM10 4V3h4v1Z" data-original="#000000"></path>
                                            <path d="M11 17v-7a1 1 0 0 0-2 0v7a1 1 0 0 0 2 0Zm4 0v-7a1 1 0 0 0-2 0v7a1 1 0 0 0 2 0Z" data-original="#000000"></path>
                                        </svg>
                                        REMOVE
                                    </button>
                                </div>
                            </div>

                            <div className="sm:ml-auto max-sm:flex max-sm:justify-between max-sm:gap-4 max-sm:col-span-full">
                                {/*ITEM PRICE*/}
                                <h4 className="text-base font-semibold text-slate-900">${item.price}</h4>

                                {/*QUANTITY*/}
                                <div className="flex items-center px-2.5 py-1.5 border border-gray-300 text-slate-900 text-sm font-medium rounded-md sm:mt-6">
                                    <small className="text-gray-400 d-block">Qty:</small>
                                    <span className="cursor-pointer">
                                        <svg xmlns="http://www.w3.org/2000/svg" class="w-2.5 fill-current" viewBox="0 0 124 124">
                                            <path d="M112 50H12C5.4 50 0 55.4 0 62s5.4 12 12 12h100c6.6 0 12-5.4 12-12s-5.4-12-12-12z" data-original="#000000"></path>
                                        </svg>
                                    </span>
                                    <span className="mx-3">{item.quantity}</span>
                                    <span className="cursor-pointer">
                                        <svg xmlns="http://www.w3.org/2000/svg" className="w-2.5 fill-current" viewBox="0 0 42 42">
                                            <path d="M37.059 16H26V4.941C26 2.224 23.718 0 21 0s-5 2.224-5 4.941V16H4.941C2.224 16 0 18.282 0 21s2.224 5 4.941 5H16v11.059C16 39.776 18.282 42 21 42s5-2.224 5-4.941V26h11.059C39.776 26 42 23.718 42 21s-2.224-5-4.941-5z" data-original="#000000"></path>
                                        </svg>
                                    </span>
                                </div>

                            </div>
                          </div>
                        </div>
                      )}
                    </div>

                    {/* Item Details */}
                    <div className="col-12 col-md-6">
                      

                      {/* Debug Info (development only) */}
                      {process.env.NODE_ENV === 'development' && (
                        <div className="mt-3 p-2 rounded border border-emerald-500/20 bg-emerald-500/10">
                          <small className="text-emerald-300">
                            <strong>Debug:</strong><br/>
                            SKU: {item.sku || 'N/A'}<br/>
                            Cockpit3D ID: {item.cockpit3d_id || 'N/A'}<br/>
                            Has CustomImage: {item.customImage?.dataUrl ? 'Yes (new)' : item.options.rawImageUrl ? 'Yes (old)' : 'No'}<br/>
                            {item.customImage && `Image Size: ${(item.customImage.fileSize / 1024).toFixed(1)}KB`}
                          </small>
                        </div>
                      )}

                      {/* Price and Actions */}
                      <div className="mt-3 pt-3 border-top border-emerald-500/20 d-flex justify-content-between align-items-center">
                        <h5 className="mb-0">
                          <span className="text-emerald-400 font-bold">${((item.price || (item as any).totalPrice || 0)).toFixed(2)}</span>
                          <small className="text-gray-400 d-block">Qty: {item.quantity}</small>
                        </h5>
                        
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Checkout Summary Column */}
           <div className="cart-summary rounded-xl border border-emerald-500/30 bg-gradient-to-br from-emerald-500/10 to-gray-900/50 p-5 sticky-top backdrop-blur-sm" style={{ top: '100px' }}>
              <h3 className="mb-4 text-emerald-300 font-bold">Order Summary</h3>
              
              <div className="d-flex justify-content-between mb-3 text-gray-200">
                <span>Subtotal:</span> <span className="font-semibold">${total.toFixed(2)}</span>
              </div>
              
              <div className="d-flex justify-content-between mb-3 pb-3 border-bottom border-emerald-500/20 text-gray-400">
                <span>Shipping:</span> <span>Calculated at checkout</span>
              </div>
              
              <div className="d-flex justify-content-between mb-4">
                <h4 className="mb-0 text-emerald-100">Total:</h4>
                <h4 className="mb-0 text-emerald-400 font-bold">${total.toFixed(2)}</h4>
              </div>

              <button
                onClick={proceedToCheckout}
                disabled={checkoutLoading}
                className="btn btn-lg p-4 w-full mb-3 bg-emerald-600 hover:bg-emerald-500 border-0 text-white font-semibold transition-all shadow-lg shadow-emerald-500/20"
              >
                {checkoutLoading ? (
                  <>
                    <span className="spinner-border spinner-border-sm me-2" />
                    Processing...
                  </>
                ) : (
                  'Proceed to Checkout'
                )}
              </button>

              <p className="text-center text-gray-400 small mb-0">
                ðŸ”’ Secure checkout powered by Stripe
              </p>             
            </div>

        </div>

      </section>

      {/* Debug Info (development only) */}
              {process.env.NODE_ENV === 'development' && (
                <div className="mt-4 pt-4 border-top">
                  <h6 className="text-warning mb-3">ðŸ”§ Developer Mode - Cart Debug</h6>
                  
                  <div className="bg-black p-3 rounded mb-3">
                    <h6 className="text-muted small mb-2">ðŸ“¦ Order Structure (Cockpit3D Format)</h6>
                    <pre className="text-white small mb-0" style={{ maxHeight: '300px', overflow: 'auto', fontSize: '11px' }}>
{JSON.stringify(buildCockpitOrder(), null, 2)}
                    </pre>
                  </div>

                  <div className="bg-black p-3 rounded">
                    <h6 className="text-muted small mb-2">ðŸ›’ Raw Cart Data</h6>
                    <pre className="text-white small mb-0" style={{ maxHeight: '300px', overflow: 'auto', fontSize: '11px' }}>
{JSON.stringify(cart, null, 2)}
                    </pre>
                  </div>
                </div>
              )}
    </div>

  )
}